.menu-projetos--corpo {
  margin: auto;
  max-width: 1920px;
  background-color: var(--cor-background);
}
.menu-projetos {
  display: flex;
  row-gap: 20px;
  margin: 0 80px;
  flex-wrap: wrap;
  column-gap: 20px;
  align-items: center;
  justify-content: center;
}

@media (max-width: 1200px) {
  .menu-projetos {
    align-items: stretch;
  }
}

@media (max-width: 950px) {
  .menu-projetos {
    margin: 0 40px;
    align-items: stretch;
  }
}

@media (max-width: 300px) {
  .menu-projetos {
    margin: 0 40px;
    align-items: stretch;
  }
}
