.footer {
  height: 7%;
  margin: auto;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  padding-top: 2%;
  margin-bottom: 0;
  align-self: center;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: var(--cor-background);
}
.gitIcon {
  font-size: 30px;
  filter: saturate(0%);
  color: var(--cor-azul__claro);
}
.gitIcon:hover {
  cursor: pointer;
  filter: saturate(100%);
  animation: jump 0.75s;
}

@keyframes jump {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}

.footer_content {
  display: flex;
  font-size: 16px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.link {
  color: #fff;
  text-decoration: none;
}

.link:hover {
  filter: saturate(100%);
  color: var(--cor-azul__claro);
}
.link::selection {
  color: white;
  background-color: blue;
}

@media screen and (max-width: 950px) {
  .footer_content {
    font-size: 15px;
  }
}

@media screen and (max-width: 400px) {
  .footer_content {
    font-size: 12px;
  }
}

@media screen and (max-width: 300px) {
  .footer_content {
    display: flex;
    flex-wrap: wrap;
    font-size: 10px;
    text-align: center;
  }
}
